<script>

    import Popup from "@/components/Blocks/Popup"
    import {mapState} from 'vuex'
    import moment from "moment"
    import {required, minLength} from 'vuelidate/lib/validators'

    export default {
        name: "CreateOrderStep2",
        components: {
            Popup
        },
        validations () {
            return {
                newOgrn: {required, minLength: minLength(this.ul ? 13 : 15)}
            }
        },
        computed: {
            ...mapState({
                order: state => state.createOrder.orderFullInfo,
                company: state => state.users.company,
                currentOrder: state => state.createOrder,
                ul: state => state.createOrder.orderFullInfo.company.ul
            })
        },
        data() {
            return {
                openInstructionModal: true,
                changedData: false,
                ogrnChange: false,
                load: false,
                newOgrn: ''
            }
        },
        methods: {
            updateStep () {
                this.$store.dispatch('updateOrderOnlyOne', {
                    id: this.$route.params.id,
                    fields: {
                        ogrn: this.newOgrn
                    }
                })
            },
            setOrderStepPlus() {
                if (this.ogrnChange && this.$v.$invalid) {
                    this.$v.$touch();
                    return
                }
                this.load = true
                if ( this.ul ) {
                    this.$store.dispatch('nextStep').then(() => {
                        this.load = false
                    })
                }
                else {
                    this.$store.dispatch('updateStep', 4).then(() => {
                        this.load = false
                    })
                }
                // if ( this.ogrnChange && this.changedData ) {
                //     this.$store.dispatch('updateOrderOnlyOne', {
                //         id: this.$route.params.id,
                //         fields: {
                //             ogrn: this.newOgrn
                //         }
                //     }).then(() => {
                //         let id = this.$route.params.id
                //         this.$store.dispatch('getCurrentOrder', id).then(resp => {
                //             if ( this.ul ) {
                //                 this.$store.dispatch('nextStep').then(() => {
                //                     this.load = false
                //                 })
                //             }
                //             else {
                //                 this.$store.dispatch('updateStep', 4).then(() => {
                //                     this.load = false
                //                 })
                //             }
                //         })
                //     })
                // }
                // else {
                //     if ( this.ul ) {
                //         this.$store.dispatch('nextStep').then(() => {
                //             this.load = false
                //         })
                //     }
                //     else {
                //         this.$store.dispatch('updateStep', 4).then(() => {
                //             this.load = false
                //         })
                //     }
                // }
            },
            setOrderStepMinus() {
                this.$store.dispatch('prevStep')
            },
            changedVal(bool) {
                this.changedData = bool
            },
            closeModal(modal) {
                this[modal] = false
            },
            parseDate(date) {
                return moment(date).format('DD.MM.YYYY')
            }
        },
        mounted() {
            this.openInstructionModal = true
            const instLinks = document.querySelectorAll('.open-instruction-modal')
            instLinks.forEach(link => {
                link.addEventListener('click', e => {
                    e.preventDefault()
                    this.openInstructionModal = true
                })
            })
        },
    }
</script>

