<template>

    <CreditSelection
        ref="creditSelection"
        :creditInfo="{rate: 1.5, commission: 10000}"
        :orderStep1="true"/>

</template>

<script>

    import CreditSelection from '@/components/Blocks/CreditSelection'
    import {mapState} from "vuex"

    export default {
        name: "CreateOrderStep1",
        components: {
            CreditSelection,
        },
        computed: {
            ...mapState({
                currentOrder: state => state.createOrder
            })
        },
        mounted() {
            let tariffId = this.currentOrder.orderFullInfo.order.tariff_id
            this.$store.dispatch('setTariffDataForOrder', tariffId)
        }
    }
</script>

