<script>

    import {required, minLength} from 'vuelidate/lib/validators'
    import Popup from "@/components/Blocks/Popup";
    import {mapState} from 'vuex'

    export default {
        name: "CreateOrderStep6",
        components: {
            Popup
        },
        validations: {
            mainActivities: {
                $each: {
                    title: {required},
                    share: {required}
                }
            },
            mainSuppliers: {
                $each: {
                    title: {required},
                    share: {required}
                }
            },
            mainBuyers: {
                $each: {
                    title: {required},
                    share: {required}
                }
            },
            holdings: {
                $each: {
                    name: {required},
                    inn: {required, minLength: minLength(12)}
                }
            }
        },
        computed: {
            ...mapState({
                order: state => state.createOrder.orderFullInfo
            }),
            mainActivities() {
                return this.order.order.share.filter(item => item.type === 1)
            },
            mainSuppliers() {
                return this.order.order.share.filter(item => item.type === 2)
            },
            mainBuyers() {
                return this.order.order.share.filter(item => item.type === 3)
            },
            buyersPopulation() {
                return this.order.order.share.filter(item => item.type === 4)[0]
            },
            holdings () {
                return this.order.order.holdings
            }
        },
        data: () => ({
            errorModal: false,
            load: false,
            shares: [
                true,
                true,
                true
            ],
            errorText: '–í–≤–µ–¥–∏—Ç–µ –æ–±—è–∑–∞—Ç–µ–ª—å–Ω—ã–µ –ø–æ–ª—è'
        }),
        methods: {
            addHoldings() {
                this.holdings.push({
                    name: '',
                    inn: ''
                })
            },
            removeHoldings(index) {
                this.holdings.splice(index, 1)
            },
            confirmData() {
                this.shares[0] = this.mainActivities.reduce((prev, curr) => prev + (curr.share ? parseFloat(curr.share) : 0), 0)
                this.shares[1] = this.mainSuppliers.reduce((prev, curr) => prev + (curr.share ? parseFloat(curr.share) : 0), 0)
                this.shares[2] = this.mainBuyers.reduce((prev, curr) => prev + (curr.share ? parseFloat(curr.share) : 0), 0)

                if (
                    this.$v.mainActivities.$each[0].$invalid ||
                    this.$v.mainSuppliers.$each[0].$invalid ||
                    this.$v.mainBuyers.$each[0].$invalid
                ) {
                    this.$v.$touch();
                    this.errorText = '–í–≤–µ–¥–∏—Ç–µ –æ–±—è–∑–∞—Ç–µ–ª—å–Ω—ã–µ –ø–æ–ª—è'
                    this.errorModal = true
                    return
                }
                if (this.shares[0] > 100) {
                    this.errorText = '–°—É–º–º–∞ –¥–æ–ª–µ–π –≤ –æ–±—â–µ–º –æ–±—ä–µ–º–µ —Ä–µ–∞–ª–∏–∑–∞—Ü–∏–∏ –≤ –æ—Å–Ω–æ–≤–Ω—ã—Ö –≤–∏–¥–∞—Ö –¥–µ—è—Ç–µ–ª—å–Ω–æ—Å—Ç–∏ –Ω–µ –º–æ–∂–µ—Ç –ø—Ä–µ–≤—ã—à–∞—Ç—å 100%'
                    this.errorModal = true
                    return
                }
                if (this.shares[1] > 100) {
                    this.errorText = '–°—É–º–º–∞ –¥–æ–ª–µ–π –≤ –æ–±—â–µ–º –æ–±—ä–µ–º–µ —Ä–µ–∞–ª–∏–∑–∞—Ü–∏–∏ —É –æ—Å–Ω–æ–≤–Ω—ã—Ö –ø–æ—Å—Ç–∞–≤—â–∏–∫–æ–≤ (–ø–æ –æ–±–æ—Ä–æ—Ç—É) –Ω–µ –º–æ–∂–µ—Ç –ø—Ä–µ–≤—ã—à–∞—Ç—å 100%'
                    this.errorModal = true
                    return
                }
                if (this.shares[2] > 100) {
                    this.errorText = '–°—É–º–º–∞ –¥–æ–ª–µ–π –≤ –æ–±—â–µ–º –æ–±—ä–µ–º–µ —Ä–µ–∞–ª–∏–∑–∞—Ü–∏–∏ —É –æ—Å–Ω–æ–≤–Ω—ã—Ö –ø–æ–∫—É–ø–∞—Ç–µ–ª–µ–π (–ø–æ –æ–±–æ—Ä–æ—Ç—É) –Ω–µ –º–æ–∂–µ—Ç –ø—Ä–µ–≤—ã—à–∞—Ç—å 100%'
                    this.errorModal = true
                    return
                }
                if (this.order.company.ul) {
                    if (this.$v.holdings.$invalid) {
                        this.$v.holdings.$touch()
                        this.errorText = '–ü—Ä–æ–≤–µ—Ä—å—Ç–µ –∫–æ—Ä—Ä–µ–∫—Ç–Ω–æ—Å—Ç—å –≤–≤–µ–¥–µ–Ω—ã—Ö –¥–∞–Ω–Ω—ã—Ö'
                        this.errorModal = true
                        return
                    }
                }
                this.setOrderStepPlus()
            },
            setOrderStepPlus() {
                this.load = true
                let fields = {share: this.order.order.share}
                if (this.order.company.ul && this.holdings.length > 0) {
                    fields.holdings = this.holdings
                }
                this.$store.dispatch('updateOrderOnlyOne', {id: this.$route.params.id, fields}).then(() => {
                    this.$store.dispatch('nextStep')
                    this.load = false
                })
            },
            setOrderStepMinus() {
                this.$store.dispatch('prevStep')
            }
        }
    }
</script>
