            </div>

            <div class="popup-body">
                <ol>
                    <li>–í—Å—Ç–∞–≤—å—Ç–µ —Ü–∏—Ñ—Ä–æ–≤–æ–π –Ω–æ—Å–∏—Ç–µ–ª—å —ç–ª–µ–∫—Ç—Ä–æ–Ω–Ω–æ–π –ø–æ–¥–ø–∏—Å–∏</li>
                    <li>–û—Ç–∫—Ä–æ–π—Ç–µ –ø—Ä–æ–≥—Ä–∞–º–º—É –ö—Ä–∏–ø—Ç–æ–ü—Ä–æ CSP –Ω–∞ –∫–æ–º–ø—å—é—Ç–µ—Ä–µ</li>
                    <li>–ù–∞–∂–º–∏—Ç–µ –Ω–∞ –≤–∫–ª–∞–¥–∫—É "–°–µ—Ä–≤–∏—Å"</li>
                    <li>–í –æ–∫–Ω–µ –≤—ã–±–µ—Ä–∏—Ç–µ –ø—É–Ω–∫—Ç "–ü—Ä–æ—Å–º–æ—Ç—Ä–µ—Ç—å —Å–µ—Ä—Ç–∏—Ñ–∏–∫–∞—Ç—ã –≤ –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä–µ"</li>
                    <li>–í –æ—Ç–∫—Ä—ã–≤—à–µ–º—Å—è –æ–∫–Ω–µ –Ω–∞–∂–º–∏—Ç–µ –Ω–∞ –∫–Ω–æ–ø–∫—É "–û–±–∑–æ—Ä". –í—ã–±–µ—Ä–∏—Ç–µ –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä –∏ –ø–æ–¥—Ç–≤–µ—Ä–¥–∏—Ç–µ —Å–≤–æ–π –≤—ã–±–æ—Ä –∫–Ω–æ–ø–∫–æ–π –û–ö</li>
                    <li>–í –æ—Ç–∫—Ä—ã–≤—à–µ–º—Å—è –æ–∫–Ω–µ –Ω–∞–∂–º–∏—Ç–µ –Ω–∞ –∫–Ω–æ–ø–∫—É "–£—Å—Ç–∞–Ω–æ–≤–∏—Ç—å". –ü–æ—Å–ª–µ —ç—Ç–æ–≥–æ —Å–æ–≥–ª–∞—Å–∏—Ç–µ—Å—å —Å –ø—Ä–µ–¥–ª–æ–∂–µ–Ω–∏–µ–º –∑–∞–º–µ–Ω–∏—Ç—å —Å–µ—Ä—Ç–∏—Ñ–∏–∫–∞—Ç</li>
                    <li>–û–±–Ω–æ–≤–∏—Ç–µ —Å—Ç—Ä–∞–Ω–∏—Ü—É –≤ –±—Ä–∞—É–∑–µ—Ä–µ</li>
                </ol>
                <p>–° –±–æ–ª–µ–µ –ø–æ–¥—Ä–æ–±–Ω–æ–π –∏–Ω—Å—Ç—Ä—É–∫—Ü–∏–µ–π –≤—ã –º–æ–∂–µ—Ç–µ –æ–∑–Ω–∞–∫–æ–º–∏—Ç—å—Å—è –ø–æ <a href="https://cryptostore.ru/article/instruktsii/kak_ustanovit_sertifikat_podpisi_cherez_kriptopro_csp/" class="link" target="_blank">—Å—Å—ã–ª–∫–µ</a></p>
            </div>

