</template>

<script>

    import DocView from "@/components/Blocks/DocView";
    import Popup from "@/components/Blocks/Popup";
    import FormGroupSwitchCheckbox from "@/components/Inputs/FormGroupSwitchCheckbox";
    import {mapState, mapGetters} from 'vuex'
    import moment from 'moment'

    import { getSystemInfo, getUserCertificates, createHash, createAttachedSignature } from 'crypto-pro'

    export default {
        name: "CreateOrderStep10",
        components: {FormGroupSwitchCheckbox, Popup, DocView},
        computed: {
            ...mapState({
                order: state => state.createOrder.orderFullInfo,
                visibleDocs: state => state.createOrder.visibleDocs,
                invisibleDocs: state => state.createOrder.invisibleDocs,
                company: state => state.users.company,
                tariffs: state => state.tariffs.tariffs
            }),
            ...mapGetters({
                targets: 'getTarrifsTargets'
            }),
            currentTarget() {
                const target = this.targets.filter(item => item.id === parseInt(this.order.order.purpose))[0]
                return target ? target.title : ''
            },
            canSigning() {
                let can = true
                const target = this.order.order.purpose
                const tarrif = this.order.order.tariff_id
                if ( this.visibleDocs.length > 0 ) {
                    this.visibleDocs.forEach(doc => {
                        doc.files.forEach(item => {
                            !item.familiarized ? can = false : null
                        })
                    })
                    if (!this.company.regbank) {
                        !this.openingCurrentAcc ? can = false : null
                        if (parseInt(target) === 1 || tarrif === 1) {
                            !this.openingPledgeAcc ? can = false : null
                        }
                    }
                }
                else {
                    can = false
                }
                return can
            },
            currentTariff () {
                return this.tariffs.filter(item => item.name === this.order.order.name)[0]
            },
            commission () {
                if (this.currentTariff) {
                    let res = 0
                    let sum = this.order.order.sum
                    res = sum * this.currentTariff.commissionPercent / 100
                    return res > this.currentTariff.minCommission ? res : this.currentTariff.minCommission
                } else {
                    return 0
                }
            }
        },
        data: () => ({
            viewDocumentModal: false,
            viewDocument: {},
            signingModal: false,
            successModal: false,
            errorModal: false,
            load: false,
            signLoad: false,
            openingCurrentAcc: false,
            openingPledgeAcc: false,
            diadocModal: false,
            errorDiadocText: '',
            errorDiadocModal: false,
            sertificates: [],
            sertificatesModal: false,
            selectedSertificates: {},
            mobModal: false,
            instructionPopup: false
        }),
        methods: {
            setOrderStepMinus() {
                this.$store.dispatch('prevStep')
            },
            openDocViewModal(doc) {
                this.viewDocument = doc
                this.viewDocumentModal = true
            },
            familiarizedDoc() {
                this.viewDocument.familiarized = true
            },
            closeViewDocModal() {
                this.viewDocumentModal = false
            },
            openSigningModal() {
                if ( this.canSigning ) {
                    this.signingModal = true
                }
                else {
                    alert('–ü—Ä–æ—á–∏—Ç–∞–π—Ç–µ –∏ –æ–∑–Ω–∞–∫–æ–º—å—Ç–µ—Å—å —Å–æ –≤—Å–µ–º–∏ –¥–æ–∫—É–º–µ–Ω—Ç–∞–º–∏')
                }
            },
            closeSuccessModal() {
                this.successModal = false
                this.$router.push('/orders')
            },
            closeDiadocModal() {
                this.diadocModal = false
            },
            closeSertificatesModal() {
                this.sertificatesModal = false
            },
            parseDate(date) {
                return moment(date).format('DD.MM.YYYY')
            },
            initDiadocScript() {
                if ( !this.load ) {
                    this.load = true
                    getSystemInfo().then(res => {
                        getUserCertificates().then(sertList => {
                            if ( sertList.length > 0 ) {
                                let sertPromises = []
                                this.sertificates = []

                                sertList.forEach(sert => {
                                    sertPromises.push(new Promise(resolve => {
                                        sert.isValid().then(() => {
                                            this.sertificates.push(sert)
                                            resolve();
                                        })
                                    }))
                                })

                                Promise.all(sertPromises).then(() => {
                                    this.load = false
                                    this.$refs.signingModal.closePopup()
                                    this.selectedSertificates = this.sertificates[0]
                                    this.sertificatesModal = true
                                })
                            }
                        }).catch(res => {
                            switch (res) {
                                case 'certificates_not_found':
                                    this.errorDiadocText = '–ù–µ –Ω–∞–π–¥–µ–Ω–æ –Ω–∏ –æ–¥–Ω–æ–≥–æ —Å–µ—Ä—Ç–∏—Ñ–∏–∫–∞—Ç–∞.'
                                    break;
                                case 'valid_certificates_not_found':
                                    this.errorDiadocText = '–ù–µ –Ω–∞–π–¥–µ–Ω–æ –Ω–∏ –æ–¥–Ω–æ–≥–æ –≤–∞–ª–∏–¥–Ω–æ–≥–æ —Å–µ—Ä—Ç–∏—Ñ–∏–∫–∞—Ç–∞.'
                                    break;
                                case 'certificate_read_error':
                                    this.errorDiadocText = '–û—à–∏–±–∫–∞ —á—Ç–µ–Ω–∏—è –æ–¥–Ω–æ–≥–æ –∏–∑ —Å–µ—Ä—Ç–∏—Ñ–∏–∫–∞—Ç–æ–≤.'
                                    break
                            }
                            this.errorDiadocModal = true
                        })
                    }).catch(error => {
                        this.load = false
                        this.diadocModal = true
                        this.$refs.signingModal.closePopup()
                    })
                }
            },
            readFile(messageFile) {
                return new Promise(function (resolve) {
                    let fileReader = new FileReader();
                    fileReader.onload = function () {
                        resolve(this.result);
                    };
                    fileReader.readAsArrayBuffer(messageFile);
                });
            },
            generateSignature(hash) {
                const sert = this.selectedSertificates
                let signaturePromise
                let ths = this
                return new Promise((resolve, reject) => {signaturePromise = createAttachedSignature(sert.thumbprint, hash)
                    signaturePromise.then(function (signature) {
                        resolve(signature)
                    }, function (error) {
                        ths.errorDiadocText = error.message
                        ths.errorDiadocModal = true
                        ths.signLoad = false
                        reject()
                    })
                })
            },
            createSignature() {
                // eslint-disable-next-line no-useless-escape
                const name = this.selectedSertificates.name.replace(/[&\/\\#,+()$~%.'":*?<>{}]/g, '').toLowerCase().trim()
                // eslint-disable-next-line no-useless-escape
                const parsedCompanyName = this.company.name.replace(/[&\/\\#,+()$~%.'":*?<>{}]/g, '').toLowerCase().trim()
                if (name !== parsedCompanyName) {
                    this.errorDiadocText = '–í—ã –≤—ã–±—Ä–∞–ª–∏ —Å–µ—Ä—Ç–∏—Ñ–∏–∫–∞—Ç, –∫–æ—Ç–æ—Ä—ã–π –Ω–µ –ø—Ä–∏–Ω–∞–¥–ª–µ–∂–∏—Ç –∫–æ–º–ø–∞–Ω–∏–∏'
                    this.errorDiadocModal = true
                    return false
                }
                this.signLoad = true
                let ths = this
                let signaturePromises = []
                let sendDataVisible = []
                let sendDataInvisible = []
                this.visibleDocs.forEach((finalDocsListItem, index) => {
                    if ( finalDocsListItem.files.length !== 0 ) {
                        sendDataVisible.push({
                            id: finalDocsListItem.id,
                            docs: []
                        })
                        finalDocsListItem.files.forEach(file => {
                            signaturePromises.push(new Promise((resolve, reject) => {
                                this.$store.dispatch('getFile', file.id).then(resp => {
                                    let fileObj = new File([resp], file.name, {type: file.format})
                                    let messagePromise = this.readFile(fileObj)
                                    messagePromise.then(message => {
                                        createHash(message).then(function() {
                                            ths.generateSignature(message).then(signature => {
                                                sendDataVisible[index].docs.push({
                                                    id: file.id,
                                                    signature
                                                })
                                                resolve()
                                            }).catch(() => {
                                                this.signLoad = false
                                            })
                                        }, function(error) {
                                            this.errorDiadocText = error.message
                                            this.errorDiadocModal = true
                                            this.signLoad = false
                                            reject()
                                        })
                                    })
                                })
                            }))
                        })
                    }
                })
                this.invisibleDocs.forEach((finalDocsListItem, index) => {
                    if ( finalDocsListItem.files.length !== 0 ) {
                        sendDataInvisible.push({
                            id: finalDocsListItem.id,
                            docs: []
                        })
                        finalDocsListItem.files.forEach(file => {
                            signaturePromises.push(new Promise((resolve, reject) => {
                                this.$store.dispatch('getFile', file.id).then(resp => {
                                    let fileObj = new File([resp], file.name, {type: file.format})
                                    let messagePromise = this.readFile(fileObj)
                                    messagePromise.then(message => {
                                        createHash(message).then(function() {
                                            ths.generateSignature(message).then(signature => {
                                                sendDataInvisible[index].docs.push({
                                                    id: file.id,
                                                    signature
                                                })
                                                resolve()
                                            }).catch(() => {
                                                this.signLoad = false
                                            })
                                        }, function(error) {
                                            this.errorDiadocText = error.message
                                            this.errorDiadocModal = true
                                            this.signLoad = false
                                            reject()
                                        })
                                    })
                                })
                            }))
                        })
                    }
                })
                Promise.all(signaturePromises).then(() => {
                    let finalPromises = []
                    sendDataVisible.forEach(docs => {
                        finalPromises.push(new Promise(resolve => {
                            this.$store.dispatch('sendSignature', docs).then(res => {
                                if (res) {
                                    resolve()
                                }
                            })
                        }))
                    })
                    sendDataInvisible.forEach(docs => {
                        finalPromises.push(new Promise(resolve => {
                            this.$store.dispatch('sendSignature', docs).then(res => {
                                if (res) {
                                    resolve()
                                }
                            })
                        }))
                    })
                    Promise.all(finalPromises).then(() => {
                        this.$store.dispatch('orderResult', this.$route.params.id).then(resp => {
                            if ( resp ) {
                                this.signLoad = false
                                this.$refs.sertificatesModal.closePopup()
                                this.successModal = true
                            }
                            else {
                                this.signLoad = false
                            }
                        })
                    }).catch(() => this.signLoad = false)
                })
            },
            downloadFile(doc) {
                this.$store.dispatch('getFile', doc.id).then(blob => {
                    this.$store.dispatch('downloadFile', {content: blob, name: doc.name, format: doc.format})
                })
            }
        },
        async mounted() {
            let id = this.$route.params.id
            this.$emit('loading', true)
            this.$store.dispatch('getDocsList', id).then(() => {
                this.$emit('loading', false)
            })
            this.$store.commit('setOrderId', id)
            if (this.tariffs.length === 0) {
                await this.$store.dispatch('getTariffs')
            }
            const checkMob = () => {
                if ( window.innerWidth < 1200 ) {
                    this.mobModal = true
                }
            }
            checkMob();
            window.addEventListener('resize', checkMob)
            this.$on('hook:beforeDestroy', () => window.removeEventListener('resize', checkMob))
        }
    }
</script>
