<template>

    <div class="order-step-info">

        <div class="order-step-info-title">
            <div
                class="h2"
                v-html="$t('create_order.expression_will_title')"/>
            <p v-html="$t('create_order.expression_will_descr')"/>
        </div>

        <FoundersList />

        <div class="order-person-info-footer-btns">
            <button class="btn btn-secondary"
                    @click.prevent="setOrderStepMinus">–ù–∞–∑–∞–¥</button>
            <div v-if="foundersNotApprove.length > 0"
                 class="need-approve">
                <div class="h6">–ù–µ–æ–±—Ö–æ–¥–∏–º–æ —Å–æ–≥–ª–∞—Å–æ–≤–∞—Ç—å —Å —É—á—Ä–µ–¥–∏—Ç–µ–ª—è–º–∏:</div>
                <ul>
                    <li v-for="founder in foundersNotApprove"
                        :key="founder.id">
                        {{founder.entity ? founder.name : returnShortName(founder.name)}}
                    </li>
                </ul>
            </div>
            <button class="btn"
                    @click.prevent="setOrderStepPlus"
                    :class="{'loading': load}"
                    :disabled="!allfoundersApprove || load">–ü—Ä–æ–¥–æ–ª–∂–∏—Ç—å
            </button>
        </div>

    </div>

</template>

<script>

    import FoundersList from '@/components/Blocks/FoundersList'

    export default {
        name: "CreateOrderStep5",
        components: {
            FoundersList
        },
        computed: {
            founders() {
                return this.$store.state.founders.founders
            },
            foundersNotApprove() {
                return this.founders.filter(item => item.status !== 5)
            },
            allfoundersApprove() {
                let can = this.foundersNotApprove.length > 0 ? false : true
                return true
            }
        },
        data: () => ({
            canGoToNextStep: false,
            load: false
        }),
        methods: {
            setOrderStepPlus() {
                this.load = true
                if ( this.allfoundersApprove ) {
                    this.$store.dispatch('nextStep').then(() => {
                        this.load = false
                    })
                }
                else {
                    alert('–ù–µ–æ–±—Ö–æ–¥–∏–º–æ —Å–æ–≥–ª–∞—Å–æ–≤–∞—Ç—å —Å–æ –≤—Å–µ–º–∏ —É—á—Ä–µ–¥–∏—Ç–µ–ª—è–º–∏')
                    this.load = false
                }
            },
            setOrderStepMinus() {
                this.$store.dispatch('prevStep')
            },
            returnShortName(name) {
                let user = name.split(' ');
                return `${user[0]} ${user[1].slice(0,1)}. ${user[2].slice(0,1)}.`
            },
        }
    }
</script>

