<template>

    <div class="founders-list">

        <div v-for="(founder, index) in founders"
             :key="index"
             class="founders-item">
            <div class="founders-item-name">
                <div class="h6">{{getPosition(founder)}}</div>
                <div class="h5">{{founder.name}}</div>
            </div>
            <div class="founders-item-proportions">
                <template v-if="founder.share > 0">
                    <div class="h6">–î–æ–ª—è</div>
                    <div class="h5">{{(founder.share * 100).toFixed(0)}}%</div>
                </template>
            </div>
            <div class="founders-item-status">

                <div v-if="founder.status !== 1"
                     :class="['stick-no-bg', founderStatus(founder.status).status]">
                    {{founderStatus(founder.status).title}}
                </div>

                <template v-if="founder.status !== 5">

                    <form v-if="!founder.ul"
                          class="founders-accept-form"
                          @submit.prevent="sendApproveMessage(index, 'phone')">
                        <input type="tel"
                               ref="phone"
                               class="form-control no-label"
                               placeholder="–¢–µ–ª–µ—Ñ–æ–Ω"
                               v-model="founder.phone"
                               :class="validationClasses('phone', index)"
                               @input="addCountryCode($event, founder)"
                               v-mask="'+7 ### ### ## ##'"/>
                        <button :class="['btn btn-sm btn-border', {'loading': founder.load}]"
                                :disabled="founder.load">–û—Ç–ø—Ä–∞–≤–∏—Ç—å
                        </button>
                    </form>

                    <form v-else
                          class="founders-accept-form"
                          @submit.prevent="sendApproveMessage(index, 'email')">
                        <input type="tel"
                               class="form-control no-label"
                               placeholder="Email"
                               v-model="founder.email"
                               :class="validationClasses('email', index)"/>
                        <button :class="['btn btn-sm btn-border', {'loading': founder.load}]"
                                :disabled="founder.load">–û—Ç–ø—Ä–∞–≤–∏—Ç—å
                        </button>
                    </form>

                </template>

            </div>
        </div>

    </div>

</template>

<script>

    import {email, minLength, required} from "vuelidate/lib/validators";
    import {mapState} from "vuex";

    export default {
        name: "FoundersList",
        validations: {
            founders: {
                required,
                $each: {
                    phone: {required, minLength: minLength(16)},
                    email: {required, email}
                }
            }
        },
        computed: {
            ...mapState({
                founders: state => state.founders.founders
            })
        },
        data: () => ({
            validationsList: [],
            load: false,
        }),
        methods: {
            founderStatus(id) {
                switch (id) {
                    case 2:
                        return {
                            title: '–û—Ç–ø—Ä–∞–≤–ª–µ–Ω–æ',
                            status: 'info'
                        }
                    case 3:
                        return {
                            title: '–û–∂–∏–¥–∞–Ω–∏–µ',
                            status: 'secondary'
                        }
                    case 4:
                        return {
                            title: '–û—Ç–∫–ª–æ–Ω–µ–Ω–æ',
                            status: 'danger'
                        }
                    case 5:
                        return {
                            title: '–°–æ–≥–ª–∞—Å–æ–≤–∞–Ω–æ',
                            status: 'success'
                        }
                    case 6:
                        return {
                            title: '–û—à–∏–±–∫–∞',
                            status: 'danger'
                        }
                }
            },
            validationClasses(type, index) {
                if ( type == 'phone' ) {
                    return {invalid: this.$v.founders.$each.$iter[`${index}`].phone.$error ? (!this.$v.founders.$each.$iter[`${index}`].required || !this.$v.founders.$each.$iter[`${index}`].minLength) : false }
                }
                else if ( type == 'email' ) {
                    return {invalid: this.$v.founders.$each.$iter[`${index}`].email.$error ? (!this.$v.founders.$each.$iter[`${index}`].email.required || !this.$v.founders.$each.$iter[`${index}`].email.email) : false }
                }
            },
            sendApproveMessage(index, type) {
                let validItem = this.$v.founders.$each.$iter[`${index}`][type]
                if (validItem.$invalid) {
                    validItem.$touch();
                    return
                }
                type == 'phone' ? this.founders[index].phone = this.$options.filters.phoneFilter(this.founders[index].phone) : null
                this.$store.dispatch('sendMessageForFounder', this.founders[index])
            },
            getPosition(founder) {
                if ( founder.user ) {
                    return `–£—á—Ä–µ–¥–∏—Ç–µ–ª—å (${founder.user.position})`
                }
                else {
                    return '–£—á—Ä–µ–¥–∏—Ç–µ–ª—å'
                }
            },
            addCountryCode(e, founder) {
                let val = e.target.value
                if ( val.indexOf('+7') !== 0 && val.charAt(0) !== '9' ) {
                    founder.phone = '+7'
                }
            },
        },
        mounted() {
            this.$store.dispatch('getFounders')
            this.$on('hook:beforedestroy', () => clearInterval(this.updateFoundersList))
        }
    }
</script>

