<template>

    <div class="upload-documents-block">

        <div class="order-step-info-title">
            <div class="h2"
                 v-html="$t('create_order.required_documents_title')" />
            <p v-html="$t('create_order.required_documents_descr')" />
        </div>

        <div class="upload-documents">

            <div v-for="doc in initialDocuments"
                 :key="doc.id"
                 class="upload-documents-item">

                <div class="upload-documents-img">
                    <div class="upload-documents-poster">
                        {{doc.files.length !== 0 ? doc.files[doc.files.length - 1].format : 'File'}}
                    </div>
                </div>

                <div
                    class="upload-documents-title"
                    :class="{'uploaded': doc.files.length !== 0}">
                    {{doc.name}}
                    <ul v-if="doc.files.length !== 0"
                        class="uploaded-files">
                        <li v-for="(file, index) in doc.files"
                            :key="file.id">
                            <a
                                href="#"
                                @click.prevent="openDocViewModal(file)">{{index + 1}}. {{file.name}}.{{file.format}}</a>
                        </li>
                    </ul>
                </div>

                <div class="upload-documents-status">
                    <template v-if="doc.loaded">
                        <div class="upload-documents-loaded">–ó–∞–≥—Ä—É–∂–∞–µ—Ç—Å—è</div>
                    </template>
                    <template v-else>
                        <template v-if="doc.files.length !== 0">
                            <div class="upload-success">
                                <div class="upload-success-text">–ó–∞–≥—Ä—É–∂–µ–Ω–æ</div>
                                <label class="upload-add-btn">
                                    <input type="file"
                                           multiple
                                           @change="selectedFile($event, doc)">
                                    <span class="icon-doc-plus"></span>
                                </label>
                                <a href="#"
                                   class="upload-success-delete"
                                   @click.prevent="deleteFiles(doc.id)">
                                    <span class="icon-trash"></span>
                                </a>
                            </div>
                        </template>
                        <template v-else>
                            <div class="upload-load">
                                <label class="upload-file-btn">
                                    <input type="file"
                                           multiple
                                           @change="selectedFile($event, doc)">
                                    <span class="btn btn-sm btn-border">–ó–∞–≥—Ä—É–∑–∏—Ç—å</span>
                                </label>
                            </div>
                        </template>
                    </template>
                </div>

            </div>

        </div>

        <div class="order-person-info-footer-btns">
            <button class="btn btn-secondary"
                    @click.prevent="setOrderStepMinus">–ù–∞–∑–∞–¥</button>
            <div class="next-step-text-info"
                 v-html="$t('create_order.required_documents_footer')" />
            <button class="btn"
                    :class="{'loading': load}"
                    :disabled="load || !disabled"
                    @click.prevent="setOrderStepPlus">–ü—Ä–æ–¥–æ–ª–∂–∏—Ç—å</button>
        </div>

        <DocView v-if="viewDocumentModal"
                 :list="viewDocument"
                 @close-modal="viewDocumentModal = false"
                 :familiarizedBtn="false"/>

        <Popup v-if="errorModal"
               :error="true"
               :classes="'error-modal'"
               @close-modal="errorModal = false">

            <div class="popup-title">
                <div class="h3">–û—à–∏–±–∫–∞ –ø—Ä–∏ –∑–∞–≥—Ä—É–∑–∫–µ</div>
            </div>

            <div class="popup-body">
                <p class="error-text">{{ errorText }}</p>
                <button class="btn btn-lg btn-block close-modal">–•–æ—Ä–æ—à–æ</button>
            </div>

        </Popup>

        <Popup v-if="docViewErrorModal"
               :error="true"
               @close-modal="docViewErrorModal = false">

            <div class="popup-title">
                <div class="h3">–î–æ–∫—É–º–µ–Ω—Ç—ã —Ñ–æ—Ä–º–∞—Ç–∞ DOC, DOCX, XLS –∏ XLSX –Ω–µ–ª—å–∑—è –ø—Ä–æ—Å–º–∞—Ç—Ä–∏–≤–∞—Ç—å —á–µ—Ä–µ–∑ –±—Ä–∞—É–∑–µ—Ä</div>
            </div>

            <div class="popup-body">
                <p>–î–ª—è –ø—Ä–æ—Å–º–æ—Ç—Ä–∞ –¥–∞–Ω–Ω–æ–≥–æ –¥–æ–∫—É–º–µ–Ω—Ç–∞ –í–∞–º –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ –µ–≥–æ —Å–∫–∞—á–∞—Ç—å</p>
                <button
                    class="btn btn-lg btn-block close-modal"
                    @click.prevent="downloadFile(downloadedDoc)">–°–∫–∞—á–∞—Ç—å</button>
            </div>

        </Popup>

    </div>

</template>

<script>

    import DocView from "@/components/Blocks/DocView"
    import Popup from "@/components/Blocks/Popup"
    import {mapState} from 'vuex'

    export default {
        name: "CreateOrderStep9",
        components: {DocView, Popup},
        computed: {
            notLoadedDocs() {
                return this.documents.filter(item => item.status != 1)
            },
            ...mapState({
                order: state => state.createOrder.orderFullInfo,
                initialDocuments: state => state.createOrder.initialDocuments
            }),
            disabled() {
                let bool = true
                this.initialDocuments.filter(item => item.files.length === 0 ).length !== 0 ? bool = false : null
                return bool
            }
        },
        data: () => ({
            viewDocumentModal: false,
            viewDocument: {},
            loadedDocsViews: [],
            load: false,
            errorModal: false,
            errorText: '',
            docViewErrorModal: false,
            downloadedDoc: ''
        }),
        methods: {
            setOrderStepPlus() {
                this.load = true
                this.$store.dispatch('nextStep').then(() => {
                    this.load = false
                })
            },
            setOrderStepMinus() {
                this.$store.dispatch('prevStep')
            },
            selectedFile (e, item) {
                let files = e.target.files
                let canLoad = true
                files.forEach(file => {
                    const format = file.name.split('').reverse().join('').split('.')[0].split('').reverse().join('')
                    const size = file.size
                    if (format !== 'jpg' &&
                        format !== 'png' &&
                        format !== 'doc' &&
                        format !== 'docx' &&
                        format !== 'pdf' &&
                        format !== 'xls' &&
                        format !== 'xlsx') {
                        this.errorText = '–î–æ–ø—É—Å—Ç–∏–º—ã–µ —Ñ–æ—Ä–º–∞—Ç—ã: JPG, PNG, DOC, DOCX, PDF, XLS, XLSX'
                        this.errorModal = true
                        item.loaded = false
                        canLoad = false
                        e.target.value = ''
                        return false
                    }
                    if (size > 3000000) {
                        this.errorText = '–†–∞–∑–º–µ—Ä —Ñ–∞–π–ª–∞ –Ω–µ –¥–æ–ª–∂–µ–Ω –ø—Ä–µ–≤—ã—à–∞—Ç—å 3 –ú–±–∞–π—Ç'
                        this.errorModal = true
                        item.loaded = false
                        canLoad = false
                        e.target.value = ''
                        return false
                    }
                    if (size === 0) {
                        this.errorText = '–§–∞–π–ª –Ω–µ –¥–æ–ø—É—Å—Ç–∏–º–æ–≥–æ —Ä–∞–∑–º–µ—Ä–∞ (0 –±–∞–π—Ç)'
                        this.errorModal = true
                        item.loaded = false
                        canLoad = false
                        e.target.value = ''
                        return false
                    }
                })
                if (canLoad) {
                    this.$store.dispatch('sendFile', {files: e.target.files, id: item.id}).then(item.loaded = false)
                }
            },
            openDocViewModal(doc) {
                if (doc.format === 'pdf' || doc.format === 'jpg' || doc.format === 'png') {
                    this.viewDocument = doc
                    this.viewDocumentModal = true
                } else {
                    this.downloadedDoc = doc
                    this.docViewErrorModal = true
                }
            },
            deleteFiles(id) {
                this.$store.dispatch('deleteFiles', id)
            },
            downloadFile(doc) {
                this.$store.dispatch('getFile', doc.id).then(blob => {
                    this.$store.dispatch('downloadFile', {content: blob, name: doc.name, format: doc.format})
                })
            }
        },
        mounted() {
            let id = this.$route.params.id
            this.$store.dispatch('getOrderDocs', {id, type: 4})
            this.$store.commit('setOrderId', id)
        }
    }
</script>

