<template>

    <div class="order-step-info">

        <div class="order-step-info-title">
            <div class="h2" v-html="$t('create_order.fast_filling_title')" />
            <p v-html="$t('create_order.fast_filling_descr')" />
        </div>

        <div class="order-person-info-list">

            <div class="order-person-info-list-item">

                <div class="order-person-info-list-title">–§–∞–º–∏–ª–∏—è</div>

                <div class="order-person-info-list-body">

                    <div class="h4">
                        {{order.user.surname || '–ù–µ —É–∫–∞–∑–∞–Ω–æ'}}
                    </div>

                </div>
            </div>

            <div class="order-person-info-list-item">

                <div class="order-person-info-list-title">–ò–º—è</div>

                <div class="order-person-info-list-body">

                    <div class="h4">
                        {{order.user.name || '–ù–µ —É–∫–∞–∑–∞–Ω–æ'}}
                    </div>

                </div>
            </div>

            <div class="order-person-info-list-item">

                <div class="order-person-info-list-title">–û—Ç—á–µ—Å—Ç–≤–æ</div>

                <div class="order-person-info-list-body">

                    <div class="h4">
                        {{order.user.patronymic || '–ù–µ —É–∫–∞–∑–∞–Ω–æ'}}
                    </div>

                </div>
            </div>

            <div class="order-person-info-list-item">

                <div class="order-person-info-list-title">–¢–µ–ª–µ—Ñ–æ–Ω</div>

                <div class="order-person-info-list-body">

                    <div class="h4">
                        {{order.user.phone | VMask('+# ### ### ## ##')}}
                    </div>

                </div>
            </div>

        </div>

        <div class="order-person-info-footer-btns">
            <button class="btn btn-secondary"
                    @click.prevent="setOrderStepMinus">–ù–∞–∑–∞–¥</button>
            <button class="btn"
                    :class="{'loading': load}"
                    :disabled="load"
                    @click.prevent="setOrderStepPlus">{{'–ü—Ä–æ–¥–æ–ª–∂–∏—Ç—å'}}</button>
        </div>

    </div>

</template>

<script>

    import {mapState} from "vuex";

    export default {
        name: "CreateOrderStep4",
        computed: {
            ...mapState({
                currentOrder: state => state.createOrder,
                order: state => state.createOrder.orderFullInfo,
            })
        },
        data: () => ({
            load: false
        }),
        methods: {
            filledInfo() {
                this.$store.dispatch('nextStep')
            },
            setOrderStepPlus() {
                this.load = true
                if ( this.currentOrder.orderFullInfo.company.ul ) {
                    this.$store.dispatch('nextStep').then(() => {
                        this.load = false
                    })
                }
                else {
                    this.$store.dispatch('updateStep', 6).then(() => {
                        this.load = false
                    })
                }
            },
            setOrderStepMinus() {
                if ( this.currentOrder.orderFullInfo.company.ul ) {
                    this.$store.dispatch('prevStep')
                }
                else {
                    this.$store.dispatch('updateStep', 2)
                }
            },
        }
    }
</script>
