<script>

    import {mapState} from 'vuex'
    import {required, minLength} from 'vuelidate/lib/validators'
    import Popup from "@/components/Blocks/Popup";

    export default {
        name: "CreateOrderStep7",
        validations: {
            address3: {
                postcode: {required, minLength: minLength(6)},
                city: {required},
                region: {required},
                street: {required},
                home: {required},
            },
            contractDetails: {
                startMaxPrice: {required},
                contractStartDate: {required},
                contractEndDate: {required},
                tenderId: {required}
            }
        },
        components: {
            Popup
        },
        data: () => ({
            load: false,
            superVisoryBoardCheckbox: false,
            directorsBoardCheckbox: false,
            otherBoardCheckbox: false,
            corporateAgreement: false,
            errorModal: false,
            addressMatches: false,
            superVisoryBoardPersonal: [''],
            directorsBoardPersonal: [''],
            otherBoardPersonal: [''],
            contractDetails: {
                startMaxPrice: null,
                contractStartDate: null,
                contractEndDate: null,
                tenderId: null
            }
        }),
        computed: {
            ...mapState({
                order: state => state.createOrder.orderFullInfo,
                fields: state => state.createOrder.fields,
                tariffs: state => state.tariffs.tariffs
            }),
            address3() {
                return this.order.address.filter(item => item.type === 3)[0]
            },
            currentTariff() {
                return this.tariffs.filter(item => item.id === this.order.order.tariff_id)[0]
            }
        },
        methods: {
            setOrderStepPlus() {
                this.load = true
                for (let key in this.address3) {
                    if (this.address3[key]) {
                        if (key === 'postcode') this.address3[key] = parseInt(this.address3[key])
                        if (key !== 'type') this.address3[key] = this.address3[key].toString()
                    }
                }
                let updatedData = {
                    address: [
                        this.address3
                    ],
                }
                if (this.order.company.ul) {
                    this.order.order.superVisoryBoard !== null ? updatedData.superVisoryBoard = this.order.order.superVisoryBoard.toString() : null
                    this.order.order.directorsBoard !== null ? updatedData.directorsBoard = this.order.order.directorsBoard.toString() : null
                    this.order.order.otherBoard !== null ? updatedData.otherBoard = this.order.order.otherBoard.toString() : null
                    this.order.order.corporateAgreement !== null ? updatedData.corporateAgreement = this.order.order.corporateAgreement.toString() : null
                }
                if (parseInt(this.order.order.purpose) === 1) {
                    updatedData.startMaxPrice = this.contractDetails.startMaxPrice
                    updatedData.contractStartDate = this.contractDetails.contractStartDate
                    updatedData.contractEndDate = this.contractDetails.contractEndDate
                    updatedData.tenderId = this.contractDetails.tenderId
                }
                console.log(updatedData)
                this.$store.dispatch('updateOrderOnlyOne', {
                    id: this.$route.params.id,
                    fields: updatedData
                }).then(() => {
                    this.$store.dispatch('nextStep').then(() => {
                        this.load = false
                    })
                })
            },
            setOrderStepMinus() {
                this.$store.dispatch('prevStep')
            },
            confirmData() {
                if (this.$v.address3.$invalid) {
                    this.$v.address3.$touch();
                    this.errorModal = true
                    return
                }
                if (parseInt(this.order.order.purpose) === 1) {
                    if (this.$v.contractDetails.$invalid) {
                        this.$v.contractDetails.$touch()
                        this.errorModal = true
                        return
                    }
                }
                if (this.order.company.ul) {
                    let superVisoryBoardPersonalFiltered = this.superVisoryBoardPersonal.filter(item => item.length > 0)
                    let directorsBoardPersonalFiltered = this.directorsBoardPersonal.filter(item => item.length > 0)
                    let otherBoardPersonalFiltered = this.otherBoardPersonal.filter(item => item.length > 0)
                    if (superVisoryBoardPersonalFiltered.length !== 0) {
                        this.order.order.superVisoryBoard = superVisoryBoardPersonalFiltered.join(', ')
                    }
                    if (directorsBoardPersonalFiltered.length !== 0) {
                        this.order.order.directorsBoard = directorsBoardPersonalFiltered.join(', ')
                    }
                    if (otherBoardPersonalFiltered.length !== 0) {
                        this.order.order.otherBoard = otherBoardPersonalFiltered.join(', ')
                    }
                    if (!this.superVisoryBoardCheckbox) this.order.order.superVisoryBoard = ''
                    if (!this.directorsBoardCheckbox) this.order.order.directorsBoard = ''
                    if (!this.otherBoardCheckbox) this.order.order.otherBoard = ''
                    if (this.superVisoryBoardCheckbox && (this.order.order.superVisoryBoard === null || this.order.order.superVisoryBoard === '')) {
                        this.errorModal = true
                        return false
                    }
                    if (this.directorsBoardCheckbox && (this.order.order.directorsBoard === null || this.order.order.directorsBoard === '')) {
                        this.errorModal = true
                        return false
                    }
                    if (this.otherBoardCheckbox && (this.order.order.otherBoard === null || this.order.order.otherBoard === '')) {
                        this.errorModal = true
                        return false
                    }
                }
                this.setOrderStepPlus()
            },
            addressMatchesChange() {
                if (this.addressMatches) {
                    this.$store.commit('addressMatchesChange', 3)
                }
            }
        },
        async mounted() {
            const id = this.$route.params.id
            await this.$store.dispatch('getCurrentOrder', id).then(() => {
                if (this.order.order.superVisoryBoard !== null) {
                    this.superVisoryBoardCheckbox = true
                    this.superVisoryBoardPersonal = this.order.order.superVisoryBoard.split(', ')
                }
                if (this.order.order.directorsBoard !== null) {
                    this.directorsBoardCheckbox = true
                    this.directorsBoardPersonal = this.order.order.directorsBoard.split(', ')
                }
                if (this.order.order.otherBoard !== null) {
                    this.otherBoardCheckbox = true
                    this.otherBoardPersonal = this.order.order.otherBoard.split(', ')
                }
                if (this.order.order.corporateAgreement !== null) {
                    this.order.order.corporateAgreement === 'true' ? this.order.order.corporateAgreement = true : this.order.order.corporateAgreement = false
                }
                this.contractDetails.startMaxPrice = this.order.order.startMaxPrice
                this.contractDetails.contractStartDate = this.order.order.contractStartDate
                this.contractDetails.contractEndDate = this.order.order.contractEndDate
                this.contractDetails.tenderId = this.order.order.tenderId
            })
            this.$store.commit('setOrderId', id)
        }
    }
</script>

