<template>

    <div class="create-order-wrapper">

        <div class="go-back-link">
            <router-link to="/"><span class="icon-arrow-left"></span>–ù–∞ –≥–ª–∞–≤–Ω—É—é</router-link>
        </div>

        <div class="order-title">
            <div class="order-title-left">
                <h1>–ó–∞—è–≤–∫–∞ –Ω–∞ –ø–æ–ª—É—á–µ–Ω–∏–µ –∫—Ä–µ–¥–∏—Ç–∞ ¬´{{currentOrder.creditName}}¬ª</h1>
            </div>
            <div class="order-date">
                <span>–î–∞—Ç–∞ —Å–æ–∑–¥–∞–Ω–∏—è –∑–∞—è–≤–∫–∏: {{parseDate(currentOrder.created_at)}}</span>
                <span>–î–∞–Ω–Ω—ã–µ —ç—Ç–æ–≥–æ —à–∞–≥–∞ —Å–æ—Ö—Ä–∞–Ω–µ–Ω—ã <span style="white-space: nowrap">{{parseTime(currentOrder.updated_at)}}</span></span>
            </div>
        </div>

        <div class="order-wrapper">

            <div class="order-steps">
                <div :class="['order-step', {'active': orderStepPos > 1}]" @click="nextGlobalStep(1)">
                    <div class="order-step-number">1</div>
                    <div class="order-step-title">–î–∞–Ω–Ω—ã–µ –æ –∫—Ä–µ–¥–∏—Ç–µ</div>
                </div>
                <div :class="['order-step', {'active': orderStepPos > 8}]" @click="nextGlobalStep(8)">
                    <div class="order-step-number">2</div>
                    <div class="order-step-title">–ò–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è –æ –∫–ª–∏–µ–Ω—Ç–µ</div>
                </div>
                <div :class="['order-step', {'active': orderStepPos > 9}]" @click="nextGlobalStep(9)">
                    <div class="order-step-number">3</div>
                    <div class="order-step-title">–ü–∞–∫–µ—Ç –¥–æ–∫—É–º–µ–Ω—Ç–æ–≤</div>
                </div>
                <div :class="['order-step', {'active': orderStepPos > 10}]" @click="nextGlobalStep(10)">
                    <div class="order-step-number">4</div>
                    <div class="order-step-title">–ü–æ–¥–ø–∏—Å–∞–Ω–∏–µ</div>
                </div>
            </div>

            <div class="order-steps-blocks">

                <div class="order-steps-block">

                    <div :class="['orders-stepline',  {'visible': orderStepPos > 1 && orderStepPos < 9}]">
                        <span :style="{width: steplineWidth + '%'}"></span>
                    </div>

                    <div class="orders-substeps" :class="{'visible': orderStepPos > 1 && orderStepPos < 9}">
                        <div class="order-substep" :class="{'active': orderStepPos > 2}" @click="nextGlobalStep(2)">–ò–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è –æ –∑–∞–µ–º—â–∏–∫–µ</div>
                        <div v-if="currentOrder.orderFullInfo.company.ul" class="order-substep" :class="{'active': orderStepPos > 3}" @click="nextGlobalStep(3)">–Æ—Ä–∏–¥–∏—á–µ—Å–∫–∏–π –∞–¥—Ä–µ—Å</div>
                        <div class="order-substep" :class="{'active': orderStepPos > 4}" @click="nextGlobalStep(4)">–ò–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è –æ —Ä—É–∫–æ–≤–æ–¥–∏—Ç–µ–ª–µ</div>
                        <div v-if="currentOrder.orderFullInfo.company.ul" class="order-substep" :class="{'active': orderStepPos > 5}" @click="nextGlobalStep(5)">–°–æ–≥–ª–∞—Å–∏–µ —É—á—Ä–µ–¥–∏—Ç–µ–ª–µ–π</div>
                        <div class="order-substep" :class="{'active': orderStepPos > 6}" @click="nextGlobalStep(6)">–î–æ–ø–æ–ª–Ω–∏—Ç–µ–ª—å–Ω—ã–µ –¥–∞–Ω–Ω—ã–µ - 1</div>
                        <div class="order-substep" :class="{'active': orderStepPos > 7}" @click="nextGlobalStep(7)">–î–æ–ø–æ–ª–Ω–∏—Ç–µ–ª—å–Ω—ã–µ –¥–∞–Ω–Ω—ã–µ - 2</div>
                        <div class="order-substep" :class="{'active': orderStepPos > 8}" @click="nextGlobalStep(8)">–î–æ–ø–æ–ª–Ω–∏—Ç–µ–ª—å–Ω—ã–µ –¥–∞–Ω–Ω—ã–µ - 3</div>
                    </div>

                    <div class="credit-selection"
                         :class="{'loading': !loaded || !stepLoaded}">


                        <div class="credit-selection-body">

                            <CreateOrderStep1 v-if="orderStepPos === 1" @loading="setLoading" />
                            <CreateOrderStep2 v-if="orderStepPos === 2" @loading="setLoading" />
                            <CreateOrderStep3 v-if="orderStepPos === 3 && currentOrder.orderFullInfo.company.ul" @loading="setLoading" />
                            <CreateOrderStep4 v-if="orderStepPos === 4" @loading="setLoading" />
                            <CreateOrderStep5 v-if="orderStepPos === 5 && currentOrder.orderFullInfo.company.ul" @loading="setLoading" />
                            <CreateOrderStep6 v-if="orderStepPos === 6" @loading="setLoading" />
                            <CreateOrderStep7 v-if="orderStepPos === 7" @loading="setLoading" />
                            <CreateOrderStep8 v-if="orderStepPos === 8" @loading="setLoading" />
                            <CreateOrderStep9 v-if="orderStepPos === 9" @loading="setLoading" />
                            <CreateOrderStep10 v-if="orderStepPos === 10" @loading="setLoading" />

                        </div>

                    </div>


                </div>

            </div>

        </div>

    </div>

</template>

<script>

    import CreateOrderStep1 from '@/components/CreateOrder/CreateOrderStep1'
    import CreateOrderStep2 from '@/components/CreateOrder/CreateOrderStep2'
    import CreateOrderStep3 from '@/components/CreateOrder/CreateOrderStep3'
    import CreateOrderStep4 from '@/components/CreateOrder/CreateOrderStep4'
    import CreateOrderStep5 from '@/components/CreateOrder/CreateOrderStep5'
    import CreateOrderStep6 from '@/components/CreateOrder/CreateOrderStep6'
    import CreateOrderStep7 from '@/components/CreateOrder/CreateOrderStep7'
    import CreateOrderStep8 from '@/components/CreateOrder/CreateOrderStep8'
    import CreateOrderStep9 from '@/components/CreateOrder/CreateOrderStep9'
    import CreateOrderStep10 from '@/components/CreateOrder/CreateOrderStep10'
    import {mapState} from 'vuex'
    import moment from 'moment'

    export default {
        name: "CreateOrder",
        components: {
            CreateOrderStep1,
            CreateOrderStep2,
            CreateOrderStep3,
            CreateOrderStep4,
            CreateOrderStep5,
            CreateOrderStep6,
            CreateOrderStep7,
            CreateOrderStep8,
            CreateOrderStep9,
            CreateOrderStep10
        },
        data: () => ({
            openSuccessModal: false,
            saveAndNext: false,
            stepLoaded: false,
            loaded: true
        }),
        computed: {
            ...mapState({
                orderStepPos: state => state.createOrder.orderStep,
                currentOrder: state => state.createOrder,
            }),
            steplineWidth() {
                let perc = 0
                if ( this.orderStepPos > 1 && this.orderStepPos < 9 ) {
                    perc = 100 * ( this.orderStepPos - 1 ) / 7
                }
                return perc
            },
        },
        watch: {
            orderStepPos () {
                console.log(43234234234)
                window.scrollTo({
                    top: 0,
                    left: 0,
                    behavior: 'smooth'
                })
            }
        },
        methods: {
            setLoading(val) {
                this.loaded = !val
            },
            goToStep(step) {
                this.$store.dispatch('updateStep', step)
            },
            parseDate(date) {
                return moment(date).format('DD.MM.YYYY')
            },
            parseTime(date) {
                let dateMoment = moment(date).locale('ru'),
                    currDate = moment(new Date()),
                    returnDate = dateMoment.format('DD MMMM');
                if ( currDate.day() === dateMoment.day() ) {
                    returnDate = `—Å–µ–≥–æ–¥–Ω—è –≤ ${dateMoment.format('HH:mm')}`
                }
                else {
                    returnDate = dateMoment.format('DD.MM.YYYY –≤ HH:mm')
                }
                return returnDate
            },
            nextGlobalStep (id) {
               if (this.orderStepPos > id) {
                    this.goToStep(id)
               }
            }
        },
        mounted() {
            let id = this.$route.params.id
            this.$store.commit('setOrderId', id)
            this.stepLoaded = false
            this.$store.dispatch('getCurrentOrder', id).then(resp => {
                this.stepLoaded = true
            })
        },
        async beforeCreate () {
            await this.$store.dispatch('getTariffs')
        }
    }
</script>
