                <button class="btn btn-lg btn-block close-modal">–•–æ—Ä–æ—à–æ</button>
            </div>

        </Popup>

    </div>

</template>

<script>

    import {email, required, minLength} from 'vuelidate/lib/validators'
    import Popup from "@/components/Blocks/Popup"
    import {mapState, mapGetters} from 'vuex'
    import createNumberMask from 'text-mask-addons/dist/createNumberMask'

    const currencyMask = createNumberMask({
        prefix: '',
        allowDecimal: true,
        includeThousandsSeparator: true,
        allowNegative: false,
        thousandsSeparatorSymbol: ''
    })

    export default {
        name: "CreateOrderStep6",
        components: {
            Popup
        },
        validations: {
            address2: {
                postcode: {required, minLength: minLength(6)},
                city: {required},
                region: {required},
                street: {required},
                home: {required},
            },
            address1: {
                postcode: {required, minLength: minLength(6)},
                city: {required},
                region: {required},
                street: {required},
                home: {required},
            },
            checkingAccount: {
                $each: {
                    title: {required},
                    bik: {required},
                    accNumber: {required}
                }
            },
            activeLoans: {
                $each: {
                    name: {required},
                    sum: {required},
                    remainder: {required},
                    maturityDate: {required},
                    facts: {required}
                }
            },
            companyEmail: {required, email},
            contactPersonEmail: {required, email},
            companySite: {required},
        },
        computed: {
            ...mapState({
                fields: state => state.createOrder.fields,
                order: state => state.createOrder.orderFullInfo,
                taxationSystems: state => state.createOrder.taxationSystems,
                taxationTypes: state => state.createOrder.taxationTypes,
                checkingAccount: state => state.createOrder.orderFullInfo.order.checkingAccount,
                activeLoans: state => state.createOrder.orderFullInfo.order.activeLoans,
                tariffs: state => state.tariffs.tariffs
            }),
            ...mapGetters([
                'address1',
                'address2'
            ]),
            currentTariff () {
                return this.tariffs.filter(item => item.id === this.order.order.tariff_id)[0]
            }
        },
        data: () => ({
            decimalMask: currencyMask,
            errorModal: false,
            load: false,
            addressMatches: false,
            companyEmail: '',
            contactPersonEmail: '',
            companySite: ''
        }),
        methods: {
            confirmData() {
                let can = true
                if (this.$v.companyEmail.$invalid) {
                    this.$v.companyEmail.$touch()
                    this.errorModal = true
                    can = false
                }
                if (this.$v.contactPersonEmail.$invalid) {
                    this.$v.contactPersonEmail.$touch()
                    this.errorModal = true
                    can = false
                }
                if (this.$v.companySite.$invalid) {
                    this.$v.companySite.$touch()
                    this.errorModal = true
                    can = false
                }
                if ( this.order.company.ul ) {
                    if (this.$v.address2.$invalid) {
                        this.$v.address2.$touch();
                        this.errorModal = true
                        can = false
                    }
                }
                else {
                    if (this.$v.address1.$invalid) {
                        this.$v.address1.$touch();
                        this.errorModal = true
                        can = false
                    }
                }
                if (this.$v.checkingAccount.$invalid || this.$v.activeLoans.$invalid) {
                    this.$v.checkingAccount.$touch()
                    this.$v.activeLoans.$touch()
                    this.errorModal = true
                    can = false
                }
                if (!can) {
                    return
                } else {
                    this.setOrderStepPlus()
                }
            },
            setOrderStepPlus() {
                this.load = true
                if ( this.order.company.ul ) {
                    for ( let key in this.address2 ) {
                        if ( this.address2[key] ) {
                            if ( key === 'postcode' ) this.address2[key] = parseInt(this.address2[key])
                            if ( key !== 'type' ) this.address2[key] = this.address2[key].toString()
                        }
                    }
                }
                else {
                    for ( let key in this.address1 ) {
                        if ( key !== 'type' && this.address1[key] ) this.address1[key] = this.address1[key].toString()
                    }
                }
                let updatedData = {
                    address: [
                        this.order.company.ul ? this.address2 : this.address1
                    ],
                    taxationSystem: this.order.order.taxationSystem,
                    checkingAccount: this.checkingAccount,
                    activeLoans: this.activeLoans,
                    companyEmail: this.companyEmail,
                    contactPersonEmail: this.contactPersonEmail,
                    companySite: this.companySite
                }
                this.$store.dispatch('updateOrderOnlyOne', {id: this.$route.params.id, fields: updatedData}).then(() => {
                    this.$store.dispatch('nextStep').then(() => {
                        this.load = false
                    })
                })
            },
            setOrderStepMinus() {
                if ( this.order.company.ul ) {
                    this.$store.dispatch('prevStep')
                }
                else {
                    this.$store.dispatch('updateStep', 4)
                }
            },
            addressMatchesChange () {
                if (this.addressMatches) {
                    this.$store.commit('addressMatchesChange', 2)
                }
            },
            addCheckingAccount () {
                this.checkingAccount.push({
                    title: '',
                    bik: '',
                    accNumber: '',
                    fileCabinet: false
                })
            },
            removeCheckingAccount (index) {
                this.checkingAccount.splice(index, 1)
            },
            addActiveLoans () {
                this.activeLoans.push({
                    name: '',
                    sum: '',
                    remainder: '',
                    maturityDate: '',
                    facts: ''
                })
            },
            removeActiveLoans (index) {
                this.activeLoans.splice(index, 1)
            },
        },
        mounted() {
            if (this.order.order.companyEmail) {
                this.companyEmail = this.order.order.companyEmail
            }
            if (this.order.order.companySite) {
                this.companySite = this.order.order.companySite
            }
            if (this.order.order.contactPersonEmail) {
                this.companySite = this.order.order.contactPersonEmail
            }
            if (!this.order.order.taxationType && this.currentTariff.id === 3) {
                this.order.order.taxationType = this.taxationTypes[0].title
            }
        }
    }
</script>
